html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0;
  font: 16px/1 sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
  margin: 0;
  padding: 0; }

main,
li {
  display: block; }

h1,
h2,
h3,
h4 {
  font-size: inherit; }

strong {
  font-weight: bold; }

a,
button {
  color: inherit;
  transition: .3s; }

a {
  text-decoration: none; }

button {
  overflow: visible;
  border: 0;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer; }

::-moz-focus-inner {
  padding: 0;
  border: 0; }

:focus {
  outline: 0; }

img {
  max-width: 100%;
  height: auto;
  border: 0; }

* + * {
  margin-top: 1.5em; }

body {
  margin: auto;
  font-family: 'Baloo Tamma', cursive;
  background-color: #7A8691; }

.wrapper {
  display: flex;
  flex-flow: wrap column;
  align-items: center;
  margin-top: 2em; }

#the-message {
  color: oldlace;
  font-size: 2em;
  width: 7em;
  text-align: center; }
  #the-message .the-surf {
    color: darkgreen;
    margin-top: 0; }

.surfs-up:before {
  content: "Surfs Up"; }

.no-surf:before {
  content: "No Surf Now"; }
